<template>
  <div class="top-menubar d-flex justify-content-between px-2 align-items-center">
    <div>
      <menu-icon
        role="button"
        @click.native="toggleMenu"
      />
    </div>
    <div style="width: 0">
      <CMLogo
        role="button"
        @click.native="$router.push('/')"
      />
    </div>
    <div class="d-flex">
<!--      <info-icon-->
<!--        role="button"-->
<!--        class="mr-1"-->
<!--      />-->
      <audio-player v-if="voiceGuides[path]" :src="voiceSrc"></audio-player>
      <bell-icon role="button"/>
    </div>
  </div>
</template>

<script>
import AudioPlayer from "@core/components/audioPlayer/index.vue";
import BellIcon from './svg-icons/BellIcon.vue'
import CMLogo from './svg-icons/CMLogo.vue'
import InfoIcon from './svg-icons/InfoIcon.vue'
import MenuIcon from './svg-icons/MenuIcon.vue'
import { voiceGuides } from "@/constant/voices";
export default {
    components: {
        MenuIcon,
        CMLogo,
        InfoIcon,
        BellIcon,
        AudioPlayer
    },
    data() {
        return {
            voiceSrc: '',
            path: '',
            voiceGuides
        }
    },
    watch: {
        $route: {
            handler: 'routeChange',
            deep: true,
            immediate: true,
        },
    },
    methods: {
        routeChange() {
            this.path = window.document.location.pathname
            if (voiceGuides[this.path]){
                this.voiceSrc = voiceGuides[this.path]
            }
        },
        toggleMenu() {
            // console.log('test')
            this.$store.commit('app/UPDATE_IS_MAIN_MENU_OPEN')
        }
    }
}
</script>

<style scoped>
.top-menubar {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 64px;
    background: #fff;
    z-index: 1020;
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);

}
</style>
