<template>
  <div>
    <div
      class="bottom-navbar d-flex flex-row-reverse justify-content-around align-items-center"
    >
      <router-link
        @click.native="closeMenu"
        v-slot="{ isExactActive, navigate }"
        to="/"
      >
        <div
          class="menu-item d-flex flex-column justify-content-between align-items-center"
          :class="{ 'active-menu-item': isExactActive }"
          @click="navigate"
        >
          <span class="menu-item-icon">
            <home-icon :active="isExactActive"/>
          </span>
          <span class="menu-item-label"> خانه </span>
        </div>
      </router-link>

      <router-link
        @click.native="closeMenu"
        v-slot="{ navigate }"
        to="/debt-registration"
      >
        <div
          class="menu-item d-flex flex-column justify-content-between align-items-center"
          :class="{ 'active-menu-item': isDebtRegisterActive }"
          @click="navigate"
        >
          <span class="menu-item-icon">
            <list-icon :active="isDebtRegisterActive"/>
          </span>
          <span class="menu-item-label">
            ثبت بدهی
          </span>
        </div>
      </router-link>

      <router-link
        @click.native="closeMenu"
        v-slot="{ navigate }"
        to="/receive-debt"
      >
        <div
          class="menu-item d-flex flex-column justify-content-between align-items-center"
          :class="{ 'active-menu-item': isReceiveDebtActive }"
          @click="navigate"
        >
          <span class="menu-item-icon">
            <money-icon :active="isReceiveDebtActive"/>
          </span>
          <span class="menu-item-label"> وصول درآمد </span>
        </div>
      </router-link>
      <router-link
        @click.native="closeMenu"
        v-slot="{ navigate }"
        to="/costs"
      >
        <div
          class="menu-item d-flex flex-column justify-content-between align-items-center"
          :class="{ 'active-menu-item': isCostPageActive }"
          @click="navigate"
        >
          <span class="menu-item-icon">
            <book-icon :active="isCostPageActive"/>
          </span>
          <span class="menu-item-label"> ثبت هزینه </span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import BookIcon from "./svg-icons/BookIcon.vue";
import HomeIcon from "./svg-icons/HomeIcon.vue";
import ListIcon from "./svg-icons/ListIcon.vue";
import MoneyIcon from "./svg-icons/MoneyIcon.vue";

export default {
    components: {
        HomeIcon,
        ListIcon,
        MoneyIcon,
        BookIcon,
    },
    computed: {
        isDebtRegisterActive() {
            if (
                this.$route.path === "/debt-registration"
                || this.$route.path === "/debt-registration/list"
                || this.$route.path === "/debt-registration/current"
                || this.$route.path === "/debt-registration/current/store"
                || this.$route.path === "/debt-registration/civil"
                || this.$route.path === "/debt-registration/civil/store"
                || this.$route.path === "/debt-registration/other"
                || this.$route.path === "/debt-registration/other/store"
                || this.$route.path === "/bill-debt-registration"
                || this.$route.path === "/bill-debt-registration/registry"

            ) {
                return true;
            }
            return false;
        },
        isReceiveDebtActive() {
            if (
                this.$route.path === "/receive-debt"
                || this.$route.path === "/receive-debt/registery"
                || this.$route.path === "/debtor"

            ) {
                return true;
            }
            return false;
        },
        isCostPageActive() {
            if (
                this.$route.path === "/costs"
                || this.$route.path === "/cost/registry"
                || this.$route.path === "/cost/update"

            ) {
                return true;
            }
            return false;
        }
    },
    methods: {
        closeMenu() {
            this.$store.commit('app/CLOSE_MAIN_MENU_OPEN')
        }
    },
};
</script>

