<template>
  <div>
    <div class="d-flex">
      <div v-show="!isPlaying" @click="play" class="cursor-pointer mx-1">
        <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-player-play" width="20" height="20"
             viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" fill="none" stroke-linecap="round"
             stroke-linejoin="round"
        >
          <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
          <path d="M7 4v16l13 -8z"/>
        </svg>
      </div>
      <div v-show="isPlaying" class="cursor-pointer" @click="rewind">
        <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-player-skip-back" width="20"
             height="20" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" fill="none" stroke-linecap="round"
             stroke-linejoin="round"
        >
          <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
          <path d="M20 5v14l-12 -7z"/>
          <line x1="4" y1="5" x2="4" y2="19"/>
        </svg>
      </div>
      <div v-show="isPlaying" class="cursor-pointer mx-1" @click="pause">
        <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-player-stop" width="20" height="20"
             viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" fill="none" stroke-linecap="round"
             stroke-linejoin="round"
        >
          <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
          <rect x="5" y="5" width="14" height="14" rx="2"/>
        </svg>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    data() {
        return {
            isPlaying: false,
            audioPlayer: null
        };
    },
    props: {
        src: {
            type: String,
        }
    },
    watch: {
        src() {
            if (!this.audioPlayer) return
            this.audioPlayer.pause();
            this.audioPlayer = null;
            this.isPlaying = false
        }
    },
    methods: {
        play() {
            this.isPlaying = true;
            if (!this.audioPlayer) {
                this.audioPlayer = new Audio(this.src); // replace with your audio file name and path
                this.audioPlayer.play();
            } else {
                this.audioPlayer.play();
            }
        },
        pause() {
            this.isPlaying = false;
            this.audioPlayer.pause();
        },
        rewind() {
            this.audioPlayer.currentTime = 0;
            this.audioPlayer.play();
        }
    },
}
</script>
